:root{
    --primary: white;

}

.btn{
    padding: 8px 20px;
    border-radius: 22px;
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 20px;
    margin-top: 30px;
}

.btn--primary{
    background: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all .3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
    margin-bottom: 2rem;
}

.btn-large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background: #fff;
    color: #242424;
    transition: all .3s ease-out;
}